import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { List } from 'react-window';
import { useNavigate } from 'react-router-dom';
import { getCandidates } from '../services/candidatesApi';
import './CandidatesList.css';

const CandidatesList = () => {
  const navigate = useNavigate();
  const [candidates, setCandidates] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [stageFilter, setStageFilter] = useState('all');
  const [sortBy, setSortBy] = useState('name');

  const stages = ['applied', 'screening', 'interview', 'offer', 'hired', 'rejected'];

  // Fetch candidates
  const fetchCandidates = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await getCandidates();
      // Ensure response is an array
      setCandidates(Array.isArray(response) ? response : []);
    } catch (err) {
      setError(err.message);
      setCandidates([]); // Set empty array on error
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchCandidates();
  }, [fetchCandidates]);

  // Filter and sort candidates
  const filteredCandidates = useMemo(() => {
    // Ensure candidates is an array
    if (!Array.isArray(candidates)) {
      return [];
    }

    let filtered = [...candidates]; // Create a copy to avoid mutating the original

    // Search filter
    if (searchTerm) {
      const term = searchTerm.toLowerCase();
      filtered = filtered.filter(candidate =>
        candidate.name.toLowerCase().includes(term) ||
        candidate.email.toLowerCase().includes(term)
      );
    }

    // Stage filter
    if (stageFilter !== 'all') {
      filtered = filtered.filter(candidate => candidate.stage === stageFilter);
    }

    // Sort
    filtered.sort((a, b) => {
      switch (sortBy) {
        case 'name':
          return a.name.localeCompare(b.name);
        case 'email':
          return a.email.localeCompare(b.email);
        case 'stage':
          return a.stage.localeCompare(b.stage);
        case 'createdAt':
          return new Date(b.createdAt) - new Date(a.createdAt);
        default:
          return 0;
      }
    });

    return filtered;
  }, [candidates, searchTerm, stageFilter, sortBy]);

  const handleCandidateClick = (candidateId) => {
    navigate(`/candidates/${candidateId}`);
  };

  const getStageColor = (stage) => {
    const colors = {
      applied: '#007bff',
      screening: '#ffc107',
      interview: '#17a2b8',
      offer: '#28a745',
      hired: '#6f42c1',
      rejected: '#dc3545'
    };
    return colors[stage] || '#6c757d';
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    });
  };

  // Row component for virtualization
  const CandidateRow = ({ index, style }) => {
    const candidate = filteredCandidates[index];
    if (!candidate) return null;

    return (
      <div
        style={style}
        className="candidate-row"
        onClick={() => handleCandidateClick(candidate.id)}
      >
        <div className="candidate-info">
          <div className="candidate-avatar">
            {candidate.name.charAt(0).toUpperCase()}
          </div>
          <div className="candidate-details">
            <h4 className="candidate-name">{candidate.name}</h4>
            <p className="candidate-email">{candidate.email}</p>
            <p className="candidate-job">
              Applied for: {candidate.jobId ? `Job ${candidate.jobId}` : 'No job specified'}
            </p>
          </div>
        </div>
        <div className="candidate-meta">
          <span
            className="stage-badge"
            style={{ backgroundColor: getStageColor(candidate.stage) }}
          >
            {candidate.stage}
          </span>
          <span className="candidate-date">
            {formatDate(candidate.createdAt)}
          </span>
        </div>
      </div>
    );
  };

  if (loading) {
    return (
      <div className="candidates-list">
        <div className="loading">Loading candidates...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="candidates-list">
        <div className="error-message">
          <h2>Error</h2>
          <p>{error}</p>
        </div>
      </div>
    );
  }

  return (
    <div className="candidates-list">
      <div className="candidates-header">
        <h2>Candidates Management</h2>
        <p>Manage and track candidate progress through the hiring pipeline</p>
      </div>

      <div className="candidates-controls">
        <div className="search-section">
          <input
            type="text"
            placeholder="Search by name or email..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="search-input"
          />
        </div>

        <div className="filters-section">
          <select
            value={stageFilter}
            onChange={(e) => setStageFilter(e.target.value)}
            className="filter-select"
          >
            <option value="all">All Stages</option>
            {stages.map(stage => (
              <option key={stage} value={stage}>
                {stage.charAt(0).toUpperCase() + stage.slice(1)}
              </option>
            ))}
          </select>

          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
            className="sort-select"
          >
            <option value="name">Sort by Name</option>
            <option value="email">Sort by Email</option>
            <option value="stage">Sort by Stage</option>
            <option value="createdAt">Sort by Date</option>
          </select>
        </div>
      </div>

      <div className="candidates-stats">
        <div className="stat-item">
          <span className="stat-number">{filteredCandidates.length}</span>
          <span className="stat-label">Total Candidates</span>
        </div>
        <div className="stat-item">
          <span className="stat-number">
            {candidates.filter(c => c.stage === 'applied').length}
          </span>
          <span className="stat-label">Applied</span>
        </div>
        <div className="stat-item">
          <span className="stat-number">
            {candidates.filter(c => c.stage === 'hired').length}
          </span>
          <span className="stat-label">Hired</span>
        </div>
      </div>

      <div className="candidates-table">
        <div className="table-header">
          <div className="header-cell">Candidate</div>
          <div className="header-cell">Stage</div>
          <div className="header-cell">Applied Date</div>
        </div>
        
        <div className="table-body">
          {filteredCandidates.length > 0 ? (
            <List
              height={600}
              itemCount={filteredCandidates.length}
              itemSize={80}
              width="100%"
            >
              {CandidateRow}
            </List>
          ) : (
            <div className="no-candidates">
              <h3>No candidates found</h3>
              <p>Try adjusting your search or filter criteria.</p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default CandidatesList;
