import React, { useState } from 'react';
import AvailableJobs from './AvailableJobs';
import CandidateAssignments from './CandidateAssignments';
import DashboardHeader from './DashboardHeader';
import './Dashboard.css';

const CandidateDashboard = ({ onLogout }) => {
  const [activeTab, setActiveTab] = useState('jobs');

  const tabs = [
    { id: 'jobs', label: 'Available Jobs', component: AvailableJobs },
    { id: 'assignments', label: 'My Assignments', component: CandidateAssignments },
    { id: 'applications', label: 'My Applications', component: () => <div className="coming-soon">Application tracking coming soon...</div> }
  ];

  const ActiveComponent = tabs.find(tab => tab.id === activeTab)?.component;

  return (
    <div className="dashboard">
      <DashboardHeader onLogout={onLogout} />
      
      <main className="dashboard-main">
        <div className="container">
          <div className="dashboard-content">
            <div className="tabs">
              {tabs.map(tab => (
                <button
                  key={tab.id}
                  className={`tab-button ${activeTab === tab.id ? 'active' : ''}`}
                  onClick={() => setActiveTab(tab.id)}
                >
                  {tab.label}
                </button>
              ))}
            </div>
            
            <div className="tab-content">
              {ActiveComponent && <ActiveComponent />}
            </div>
          </div>
        </div>
      </main>
    </div>
  );
};

export default CandidateDashboard;
