import React, { useState } from 'react';
import './LoginPortal.css';

const LoginPortal = ({ onLogin }) => {
  const [selectedRole, setSelectedRole] = useState(null);
  const [userData, setUserData] = useState({
    name: '',
    email: ''
  });

  const handleRoleSelect = (role) => {
    setSelectedRole(role);
    // Set default user data based on role
    if (role === 'candidate') {
      setUserData({
        name: 'John Doe',
        email: 'john.doe@email.com'
      });
    } else if (role === 'hr') {
      setUserData({
        name: 'Sarah Johnson',
        email: 'sarah.johnson@company.com'
      });
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setUserData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleLogin = () => {
    if (selectedRole && userData.name && userData.email) {
      onLogin(selectedRole, userData);
    }
  };

  return (
    <div className="login-portal">
      <div className="login-container">
        <div className="login-header">
          <h1>TalentFlow</h1>
          <p>A Mini Hiring Platform</p>
        </div>
        
        <div className="login-form-container">
          <h2>Welcome to TalentFlow</h2>
          <p>Please select your role and enter your details:</p>
          
          <div className="role-selection">
            <h3>Select Role:</h3>
            <div className="role-buttons">
              <button 
                type="button"
                className={`btn role-btn ${selectedRole === 'candidate' ? 'btn-primary' : 'btn-outline'}`}
                onClick={() => handleRoleSelect('candidate')}
              >
                Login as Candidate
              </button>
              <button 
                type="button"
                className={`btn role-btn ${selectedRole === 'hr' ? 'btn-primary' : 'btn-outline'}`}
                onClick={() => handleRoleSelect('hr')}
              >
                Login as HR Manager
              </button>
            </div>
          </div>

          {selectedRole && (
            <div className="user-details-form">
              <h3>Your Details:</h3>
              <div className="form-group">
                <label htmlFor="name">Full Name:</label>
                <input
                  type="text"
                  id="name"
                  name="name"
                  value={userData.name}
                  onChange={handleInputChange}
                  placeholder="Enter your full name"
                  className="form-control"
                />
              </div>
              <div className="form-group">
                <label htmlFor="email">Email:</label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  value={userData.email}
                  onChange={handleInputChange}
                  placeholder="Enter your email"
                  className="form-control"
                />
              </div>
              <button 
                type="button"
                className="btn btn-primary login-btn"
                onClick={handleLogin}
                disabled={!userData.name || !userData.email}
              >
                Continue to Dashboard
              </button>
            </div>
          )}
        </div>
        
        <div className="login-footer">
          <p>Choose your role to access the appropriate dashboard</p>
        </div>
      </div>
    </div>
  );
};

export default LoginPortal;

