import React, { useState, useEffect, useCallback } from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import JobCard from './JobCard';
import JobModal from './JobModal';
import { getJobs, reorderJobs, toggleJobStatus } from '../services/jobsApi';
import './JobsBoard.css';

const JobsBoard = () => {
  const [jobs, setJobs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [editingJob, setEditingJob] = useState(null);
  const [filters, setFilters] = useState({
    search: '',
    status: '',
    page: 1,
    pageSize: 10,
    sort: 'order'
  });
  const [pagination, setPagination] = useState({
    total: 0,
    totalPages: 0,
    currentPage: 1
  });

  // Fetch jobs from API
  const fetchJobs = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await getJobs(filters);
      setJobs(response.data);
      setPagination(response.pagination);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  }, [filters]);

  // Load jobs on component mount and when filters change
  useEffect(() => {
    fetchJobs();
  }, [fetchJobs]);

  // Handle drag and drop reordering
  const handleDragEnd = async (result) => {
    if (!result.destination) return;

    const { source, destination } = result;
    const sourceIndex = source.index;
    const destinationIndex = destination.index;

    if (sourceIndex === destinationIndex) return;

    const job = jobs[sourceIndex];
    const fromOrder = job.order;
    const toOrder = jobs[destinationIndex].order;

    // Optimistic update
    const newJobs = Array.from(jobs);
    const [reorderedJob] = newJobs.splice(sourceIndex, 1);
    newJobs.splice(destinationIndex, 0, reorderedJob);
    setJobs(newJobs);

    try {
      await reorderJobs(job.id, fromOrder, toOrder);
      // Refresh the list to get the correct order from server
      await fetchJobs();
    } catch (err) {
      setError(err.message);
      // Rollback on failure
      await fetchJobs();
    }
  };

  // Handle job status toggle (archive/unarchive)
  const handleToggleStatus = async (jobId, currentStatus) => {
    const newStatus = currentStatus === 'active' ? 'archived' : 'active';
    
    try {
      await toggleJobStatus(jobId, newStatus);
      await fetchJobs();
    } catch (err) {
      setError(err.message);
    }
  };

  // Handle job edit
  const handleEditJob = (job) => {
    setEditingJob(job);
    setShowModal(true);
  };

  // Handle job creation
  const handleCreateJob = () => {
    setEditingJob(null);
    setShowModal(true);
  };

  // Handle modal close
  const handleCloseModal = () => {
    setShowModal(false);
    setEditingJob(null);
  };

  // Handle successful job save
  const handleJobSaved = () => {
    handleCloseModal();
    fetchJobs();
  };

  // Handle filter changes
  const handleFilterChange = (newFilters) => {
    setFilters(prev => ({ ...prev, ...newFilters, page: 1 }));
  };

  // Handle pagination
  const handlePageChange = (page) => {
    setFilters(prev => ({ ...prev, page }));
  };

  if (loading && jobs.length === 0) {
    return (
      <div className="jobs-board">
        <div className="loading">Loading jobs...</div>
      </div>
    );
  }

  return (
    <div className="jobs-board">
      <div className="jobs-header">
        <h2>Jobs Management</h2>
        <button 
          className="btn btn-primary"
          onClick={handleCreateJob}
        >
          Create New Job
        </button>
      </div>

      {error && (
        <div className="error-message">
          {error}
        </div>
      )}

      <div className="jobs-filters">
        <div className="filter-group">
          <input
            type="text"
            placeholder="Search jobs..."
            value={filters.search}
            onChange={(e) => handleFilterChange({ search: e.target.value })}
            className="form-control"
          />
        </div>
        <div className="filter-group">
          <select
            value={filters.status}
            onChange={(e) => handleFilterChange({ status: e.target.value })}
            className="form-control"
          >
            <option value="">All Status</option>
            <option value="active">Active</option>
            <option value="archived">Archived</option>
          </select>
        </div>
        <div className="filter-group">
          <select
            value={filters.sort}
            onChange={(e) => handleFilterChange({ sort: e.target.value })}
            className="form-control"
          >
            <option value="order">Order</option>
            <option value="title">Title</option>
            <option value="createdAt">Date Created</option>
          </select>
        </div>
      </div>

      <div className="jobs-stats">
        <span>Total: {pagination.total} jobs</span>
        <span>Showing: {jobs.length} jobs</span>
      </div>

      <DragDropContext onDragEnd={handleDragEnd}>
        <Droppable droppableId="jobs-list">
          {(provided) => (
            <div
              {...provided.droppableProps}
              ref={provided.innerRef}
              className="jobs-list"
            >
              {jobs.map((job, index) => (
                <Draggable key={job.id} draggableId={job.id} index={index}>
                  {(provided, snapshot) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      {...provided.dragHandleProps}
                      className={`job-item ${snapshot.isDragging ? 'dragging' : ''}`}
                    >
                      <JobCard
                        job={job}
                        onEdit={handleEditJob}
                        onToggleStatus={handleToggleStatus}
                      />
                    </div>
                  )}
                </Draggable>
              ))}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>

      {pagination.totalPages > 1 && (
        <div className="pagination">
          <button
            onClick={() => handlePageChange(pagination.currentPage - 1)}
            disabled={pagination.currentPage === 1}
            className="btn btn-secondary"
          >
            Previous
          </button>
          <span className="page-info">
            Page {pagination.currentPage} of {pagination.totalPages}
          </span>
          <button
            onClick={() => handlePageChange(pagination.currentPage + 1)}
            disabled={pagination.currentPage === pagination.totalPages}
            className="btn btn-secondary"
          >
            Next
          </button>
        </div>
      )}

      {showModal && (
        <JobModal
          job={editingJob}
          onClose={handleCloseModal}
          onSave={handleJobSaved}
        />
      )}
    </div>
  );
};

export default JobsBoard;

