import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { getAvailableJobs } from '../services/jobsApi';
import JobApplicationModal from './JobApplicationModal';
import './AvailableJobs.css';

const AvailableJobs = () => {
  const navigate = useNavigate();
  const [jobs, setJobs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [filters, setFilters] = useState({
    search: '',
    page: 1,
    pageSize: 12
  });
  const [pagination, setPagination] = useState({
    total: 0,
    totalPages: 0,
    currentPage: 1
  });
  const [showApplicationModal, setShowApplicationModal] = useState(false);
  const [selectedJob, setSelectedJob] = useState(null);

  // Fetch available jobs
  const fetchJobs = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await getAvailableJobs(filters);
      setJobs(response.data);
      setPagination(response.pagination);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  }, [filters]);

  useEffect(() => {
    fetchJobs();
  }, [fetchJobs]);

  const handleSearchChange = (e) => {
    setFilters(prev => ({
      ...prev,
      search: e.target.value,
      page: 1
    }));
  };

  const handlePageChange = (page) => {
    setFilters(prev => ({ ...prev, page }));
  };

  const handleApplyJob = (job) => {
    setSelectedJob(job);
    setShowApplicationModal(true);
  };

  const handleApplicationSuccess = () => {
    setShowApplicationModal(false);
    setSelectedJob(null);
    // Show success message
    alert('Application submitted successfully! You will receive an assessment assignment shortly.');
  };

  const handleViewDetails = (jobId) => {
    navigate(`/jobs/${jobId}`);
  };

  if (loading && jobs.length === 0) {
    return (
      <div className="available-jobs">
        <div className="loading">Loading available jobs...</div>
      </div>
    );
  }

  return (
    <div className="available-jobs">
      <div className="jobs-header">
        <h2>Available Job Positions</h2>
        <p>Find your next career opportunity</p>
      </div>

      {error && (
        <div className="error-message">
          {error}
        </div>
      )}

      <div className="jobs-search">
        <input
          type="text"
          placeholder="Search jobs by title or skills..."
          value={filters.search}
          onChange={handleSearchChange}
          className="form-control search-input"
        />
      </div>

      <div className="jobs-stats">
        <span>{pagination.total} positions available</span>
        {filters.search && <span>Filtered results</span>}
      </div>

      <div className="jobs-grid">
        {jobs.map((job) => (
          <div key={job.id} className="job-card" onClick={() => handleViewDetails(job.id)}>
            <div className="job-header">
              <h3 className="job-title">{job.title}</h3>
              <span className="status-badge active">Active</span>
            </div>

            <div className="job-content">
              <div className="job-meta">
                <span className="job-id">#{job.slug}</span>
              </div>

              {job.tags && job.tags.length > 0 && (
                <div className="job-tags">
                  {job.tags.slice(0, 4).map((tag, index) => (
                    <span key={index} className="tag">
                      {tag}
                    </span>
                  ))}
                  {job.tags.length > 4 && (
                    <span className="tag-more">+{job.tags.length - 4} more</span>
                  )}
                </div>
              )}

              <div className="job-dates">
                <span>Posted: {new Date(job.createdAt).toLocaleDateString()}</span>
              </div>
            </div>

            <div className="job-actions">
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  handleApplyJob(job);
                }}
                className="btn btn-primary apply-btn"
              >
                Apply Now
              </button>
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  handleViewDetails(job.id);
                }}
                className="btn btn-secondary"
              >
                View Details
              </button>
            </div>
          </div>
        ))}
      </div>

      {jobs.length === 0 && !loading && (
        <div className="no-jobs">
          <h3>No jobs found</h3>
          <p>Try adjusting your search criteria or check back later for new opportunities.</p>
        </div>
      )}

      {pagination.totalPages > 1 && (
        <div className="pagination">
          <button
            onClick={() => handlePageChange(pagination.currentPage - 1)}
            disabled={pagination.currentPage === 1}
            className="btn btn-secondary"
          >
            Previous
          </button>
          <span className="page-info">
            Page {pagination.currentPage} of {pagination.totalPages}
          </span>
          <button
            onClick={() => handlePageChange(pagination.currentPage + 1)}
            disabled={pagination.currentPage === pagination.totalPages}
            className="btn btn-secondary"
          >
            Next
          </button>
        </div>
      )}

      {showApplicationModal && selectedJob && (
        <JobApplicationModal
          job={selectedJob}
          onClose={() => {
            setShowApplicationModal(false);
            setSelectedJob(null);
          }}
          onSuccess={handleApplicationSuccess}
        />
      )}
    </div>
  );
};

export default AvailableJobs;
