import React, { useState } from 'react';
import { Routes, Route } from 'react-router-dom';
import JobsBoard from './JobsBoard';
import AssessmentBuilder from './AssessmentBuilder';
import CandidatesList from './CandidatesList';
import CandidatesKanban from './CandidatesKanban';
import DashboardHeader from './DashboardHeader';
import './Dashboard.css';

const HRDashboard = ({ onLogout }) => {
  const [activeTab, setActiveTab] = useState('jobs');

  const tabs = [
    { id: 'jobs', label: 'Jobs Management', component: JobsBoard },
    { id: 'candidates', label: 'Candidates', component: CandidatesList },
    { id: 'kanban', label: 'Pipeline', component: CandidatesKanban },
    { id: 'assessments', label: 'Assessments', component: () => <div className="coming-soon">Assessment builder coming soon...</div> }
  ];

  const ActiveComponent = tabs.find(tab => tab.id === activeTab)?.component;

  return (
    <div className="dashboard">
      <DashboardHeader onLogout={onLogout} />
      
      <main className="dashboard-main">
        <div className="container">
          <div className="dashboard-content">
            <div className="tabs">
              {tabs.map(tab => (
                <button
                  key={tab.id}
                  className={`tab-button ${activeTab === tab.id ? 'active' : ''}`}
                  onClick={() => setActiveTab(tab.id)}
                >
                  {tab.label}
                </button>
              ))}
            </div>
            
            <div className="tab-content">
              <Routes>
                <Route path="/" element={ActiveComponent && <ActiveComponent />} />
                <Route path="/assessments/:jobId" element={<AssessmentBuilder />} />
              </Routes>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
};

export default HRDashboard;
