import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate, useNavigate } from 'react-router-dom';
import LoginPortal from './components/LoginPortal';
import HRDashboard from './components/HRDashboard';
import CandidateDashboard from './components/CandidateDashboard';
import JobDetail from './components/JobDetail';
import AssessmentBuilder from './components/AssessmentBuilder';
import CandidateProfile from './components/CandidateProfile';
import AssignmentCompletion from './components/AssignmentCompletion';
import { UserProvider, useUser } from './contexts/UserContext';
import { initializeDB } from './utils/database';
import { initializeMSW } from './mocks/browser';

function ProtectedRoute({ isAuthenticated, allowedRole, userRole, children }) {
  if (!isAuthenticated) {
    return <Navigate to="/login" replace />;
  }
  if (allowedRole && userRole !== allowedRole) {
    return <Navigate to={userRole === 'hr' ? '/hr' : '/candidate'} replace />;
  }
  return children;
}

function AppContent() {
  const navigate = useNavigate();
  const { user, userType, login, logout, isAuthenticated } = useUser();
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const initializeApp = async () => {
      try {
        // Initialize MSW for API mocking
        await initializeMSW();
        
        // Initialize IndexedDB
        await initializeDB();
      } catch (error) {
        console.error('Failed to initialize app:', error);
      } finally {
        setIsLoading(false);
      }
    };

    initializeApp();
  }, []);

  const handleLogin = (type, userData = {}) => {
    login(userData, type);
    navigate(type === 'hr' ? '/hr' : '/candidate');
  };

  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  if (isLoading) {
    return (
      <div className="container">
        <div className="text-center mt-4">
          <h2>Loading TalentFlow...</h2>
        </div>
      </div>
    );
  }

  return (
    <div className="App">
      <Routes>
        <Route
          path="/login"
          element={<LoginPortal onLogin={handleLogin} />}
        />

        <Route
          path="/hr"
          element={
            <ProtectedRoute isAuthenticated={isAuthenticated} allowedRole="hr" userRole={userType}>
              <HRDashboard onLogout={handleLogout} />
            </ProtectedRoute>
          }
        />

        <Route
          path="/candidate"
          element={
            <ProtectedRoute isAuthenticated={isAuthenticated} allowedRole="candidate" userRole={userType}>
              <CandidateDashboard onLogout={handleLogout} />
            </ProtectedRoute>
          }
        />

        <Route
          path="/jobs/:jobId"
          element={
            <ProtectedRoute isAuthenticated={isAuthenticated} userRole={userType}>
              <JobDetail />
            </ProtectedRoute>
          }
        />
        <Route
          path="/assessments/:jobId"
          element={
            <ProtectedRoute isAuthenticated={isAuthenticated} userRole={userType}>
              <AssessmentBuilder />
            </ProtectedRoute>
          }
        />
        <Route
          path="/candidates/:candidateId"
          element={
            <ProtectedRoute isAuthenticated={isAuthenticated} userRole={userType}>
              <CandidateProfile />
            </ProtectedRoute>
          }
        />
        <Route
          path="/assignments/:assignmentId"
          element={
            <ProtectedRoute isAuthenticated={isAuthenticated} userRole={userType}>
              <AssignmentCompletion />
            </ProtectedRoute>
          }
        />

        <Route
          path="/"
          element={
            userType ? (
              <Navigate to={userType === 'hr' ? '/hr' : '/candidate'} replace />
            ) : (
              <Navigate to="/login" replace />
            )
          }
        />

        <Route
          path="*"
          element={
            <Navigate to={userType ? (userType === 'hr' ? '/hr' : '/candidate') : '/login'} replace />
          }
        />
      </Routes>
    </div>
  );
}

function App() {
  return (
    <UserProvider>
      <Router>
        <AppContent />
      </Router>
    </UserProvider>
  );
}

export default App;
