import React, { useState, useEffect, useCallback } from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { getCandidates, updateCandidateStage } from '../services/candidatesApi';
import './CandidatesKanban.css';

const CandidatesKanban = () => {
  const [candidates, setCandidates] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const stages = [
    { id: 'applied', title: 'Applied', color: '#007bff' },
    { id: 'screening', title: 'Screening', color: '#ffc107' },
    { id: 'interview', title: 'Interview', color: '#17a2b8' },
    { id: 'offer', title: 'Offer', color: '#28a745' },
    { id: 'hired', title: 'Hired', color: '#6f42c1' },
    { id: 'rejected', title: 'Rejected', color: '#dc3545' }
  ];

  const fetchCandidates = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await getCandidates();
      // Ensure response is an array
      setCandidates(Array.isArray(response) ? response : []);
    } catch (err) {
      setError(err.message);
      setCandidates([]); // Set empty array on error
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchCandidates();
  }, [fetchCandidates]);

  const getCandidatesByStage = (stageId) => {
    if (!Array.isArray(candidates)) {
      return [];
    }
    return candidates.filter(candidate => candidate.stage === stageId);
  };

  const handleDragEnd = async (result) => {
    const { destination, source, draggableId } = result;

    if (!destination) return;
    if (destination.droppableId === source.droppableId && destination.index === source.index) return;

    const candidateId = draggableId;
    const newStage = destination.droppableId;

    // Optimistic update
    setCandidates(prevCandidates =>
      prevCandidates.map(candidate =>
        candidate.id === candidateId
          ? { ...candidate, stage: newStage }
          : candidate
      )
    );

    try {
      await updateCandidateStage(candidateId, newStage);
    } catch (error) {
      // Revert on error
      setCandidates(prevCandidates =>
        prevCandidates.map(candidate =>
          candidate.id === candidateId
            ? { ...candidate, stage: source.droppableId }
            : candidate
        )
      );
      setError('Failed to update candidate stage');
    }
  };

  const getInitials = (name) => {
    return name
      .split(' ')
      .map(word => word.charAt(0))
      .join('')
      .toUpperCase()
      .slice(0, 2);
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric'
    });
  };

  if (loading) {
    return (
      <div className="candidates-kanban">
        <div className="loading">Loading candidates...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="candidates-kanban">
        <div className="error-message">
          <h2>Error</h2>
          <p>{error}</p>
        </div>
      </div>
    );
  }

  return (
    <div className="candidates-kanban">
      <div className="kanban-header">
        <h2>Candidates Pipeline</h2>
        <p>Drag and drop candidates between stages to manage their progress</p>
      </div>

      <DragDropContext onDragEnd={handleDragEnd}>
        <div className="kanban-board">
          {stages.map(stage => (
            <div key={stage.id} className="kanban-column">
              <div className="column-header" style={{ borderColor: stage.color }}>
                <h3 style={{ color: stage.color }}>{stage.title}</h3>
                <span className="candidate-count">
                  {getCandidatesByStage(stage.id).length}
                </span>
              </div>

              <Droppable droppableId={stage.id}>
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    {...provided.droppableProps}
                    className={`column-content ${snapshot.isDraggingOver ? 'dragging-over' : ''}`}
                    style={{ borderColor: stage.color }}
                  >
                    {getCandidatesByStage(stage.id).map((candidate, index) => (
                      <Draggable
                        key={candidate.id}
                        draggableId={candidate.id}
                        index={index}
                      >
                        {(provided, snapshot) => (
                          <div
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                            className={`candidate-card ${snapshot.isDragging ? 'dragging' : ''}`}
                          >
                            <div className="candidate-avatar">
                              {getInitials(candidate.name)}
                            </div>
                            <div className="candidate-info">
                              <h4 className="candidate-name">{candidate.name}</h4>
                              <p className="candidate-email">{candidate.email}</p>
                              <p className="candidate-date">
                                {formatDate(candidate.createdAt)}
                              </p>
                            </div>
                          </div>
                        )}
                      </Draggable>
                    ))}
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            </div>
          ))}
        </div>
      </DragDropContext>
    </div>
  );
};

export default CandidatesKanban;
