import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { getCandidateAssignments, submitAssignmentResponse } from '../services/applicationsApi';
import './AssignmentCompletion.css';

const AssignmentCompletion = () => {
  const { assignmentId } = useParams();
  const navigate = useNavigate();
  const [assignment, setAssignment] = useState(null);
  const [responses, setResponses] = useState({});
  const [loading, setLoading] = useState(true);
  const [submitting, setSubmitting] = useState(false);
  const [error, setError] = useState(null);
  const [timeLeft, setTimeLeft] = useState(null);

  useEffect(() => {
    fetchAssignment();
  }, [assignmentId]);

  useEffect(() => {
    if (assignment && assignment.validUntil) {
      const validUntil = new Date(assignment.validUntil);
      const now = new Date();
      const diff = validUntil - now;

      if (diff > 0) {
        setTimeLeft(Math.floor(diff / 1000 / 60)); // minutes
        
        const timer = setInterval(() => {
          const newDiff = validUntil - new Date();
          if (newDiff > 0) {
            setTimeLeft(Math.floor(newDiff / 1000 / 60));
          } else {
            setTimeLeft(0);
            clearInterval(timer);
          }
        }, 60000); // Update every minute

        return () => clearInterval(timer);
      } else {
        setTimeLeft(0);
      }
    }
  }, [assignment]);

  const fetchAssignment = async () => {
    try {
      setLoading(true);
      setError(null);
      
      // Get all assignments and find the specific one
      const assignments = await getCandidateAssignments('candidate-1'); // Mock candidate ID
      const foundAssignment = assignments.find(a => a.id === assignmentId);
      
      if (foundAssignment) {
        setAssignment(foundAssignment);
      } else {
        setError('Assignment not found');
      }
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (questionId, value) => {
    setResponses(prev => ({
      ...prev,
      [questionId]: value
    }));
  };

  const validateQuestion = (question) => {
    const value = responses[question.id];
    
    if (question.required && (!value || value === '')) {
      return 'This field is required';
    }
    
    if (question.type === 'numeric' && value !== '') {
      const numValue = parseFloat(value);
      if (isNaN(numValue)) {
        return 'Please enter a valid number';
      }
      if (question.min !== undefined && numValue < question.min) {
        return `Value must be at least ${question.min}`;
      }
      if (question.max !== undefined && numValue > question.max) {
        return `Value must be at most ${question.max}`;
      }
    }
    
    if (question.type === 'short-text' && value && value.length > question.maxLength) {
      return `Text must be no more than ${question.maxLength} characters`;
    }
    
    if (question.type === 'long-text' && value && value.length > question.maxLength) {
      return `Text must be no more than ${question.maxLength} characters`;
    }
    
    return null;
  };

  const shouldShowQuestion = (question) => {
    if (!question.conditionalLogic) return true;
    
    const { condition, value, operator } = question.conditionalLogic;
    const conditionResponse = responses[condition];
    
    if (!conditionResponse) return false;
    
    switch (operator) {
      case 'equals':
        return conditionResponse === value;
      case 'not_equals':
        return conditionResponse !== value;
      case 'contains':
        return conditionResponse.includes(value);
      default:
        return true;
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (timeLeft === 0) {
      setError('This assignment has expired');
      return;
    }

    // Validate all questions
    const errors = {};
    let hasErrors = false;
    
    assignment.assessment.sections.forEach(section => {
      section.questions.forEach(question => {
        if (shouldShowQuestion(question)) {
          const error = validateQuestion(question);
          if (error) {
            errors[question.id] = error;
            hasErrors = true;
          }
        }
      });
    });
    
    if (hasErrors) {
      setError('Please fix all validation errors before submitting');
      return;
    }

    try {
      setSubmitting(true);
      await submitAssignmentResponse(assignmentId, responses);
      alert('Assignment submitted successfully!');
      navigate('/candidates/assignments');
    } catch (err) {
      setError(err.message);
    } finally {
      setSubmitting(false);
    }
  };

  const renderQuestion = (question) => {
    if (!shouldShowQuestion(question)) return null;
    
    const value = responses[question.id] || '';
    const error = validateQuestion(question);
    
    return (
      <div key={question.id} className="question-item">
        <label className="question-label">
          {question.title}
          {question.required && <span className="required">*</span>}
        </label>
        
        {question.type === 'single-choice' && (
          <div className="radio-group">
            {question.options.map((option, index) => (
              <label key={index} className="radio-option">
                <input
                  type="radio"
                  name={question.id}
                  value={option}
                  checked={value === option}
                  onChange={(e) => handleInputChange(question.id, e.target.value)}
                />
                <span>{option}</span>
              </label>
            ))}
          </div>
        )}
        
        {question.type === 'multi-choice' && (
          <div className="checkbox-group">
            {question.options.map((option, index) => (
              <label key={index} className="checkbox-option">
                <input
                  type="checkbox"
                  value={option}
                  checked={Array.isArray(value) && value.includes(option)}
                  onChange={(e) => {
                    const currentValues = Array.isArray(value) ? value : [];
                    const newValues = e.target.checked
                      ? [...currentValues, option]
                      : currentValues.filter(v => v !== option);
                    handleInputChange(question.id, newValues);
                  }}
                />
                <span>{option}</span>
              </label>
            ))}
          </div>
        )}
        
        {question.type === 'short-text' && (
          <input
            type="text"
            value={value}
            onChange={(e) => handleInputChange(question.id, e.target.value)}
            maxLength={question.maxLength}
            className={`form-control ${error ? 'error' : ''}`}
            placeholder="Enter your answer..."
          />
        )}
        
        {question.type === 'long-text' && (
          <textarea
            value={value}
            onChange={(e) => handleInputChange(question.id, e.target.value)}
            maxLength={question.maxLength}
            rows={4}
            className={`form-control ${error ? 'error' : ''}`}
            placeholder="Enter your answer..."
          />
        )}
        
        {question.type === 'numeric' && (
          <input
            type="number"
            value={value}
            onChange={(e) => handleInputChange(question.id, e.target.value)}
            min={question.min}
            max={question.max}
            className={`form-control ${error ? 'error' : ''}`}
            placeholder={`Enter a number between ${question.min} and ${question.max}`}
          />
        )}
        
        {question.type === 'file-upload' && (
          <div className="file-upload">
            <input
              type="file"
              onChange={(e) => handleInputChange(question.id, e.target.files[0]?.name || '')}
              className="form-control"
            />
            <small>Upload a file</small>
          </div>
        )}
        
        {error && <span className="error-text">{error}</span>}
        
        {question.type === 'short-text' && question.maxLength && (
          <small className="char-count">
            {value.length}/{question.maxLength} characters
          </small>
        )}
        
        {question.type === 'long-text' && question.maxLength && (
          <small className="char-count">
            {value.length}/{question.maxLength} characters
          </small>
        )}
      </div>
    );
  };

  const isExpired = timeLeft === 0;
  const isSubmitted = assignment?.status === 'submitted';

  if (loading) {
    return (
      <div className="assignment-completion">
        <div className="loading">Loading assignment...</div>
      </div>
    );
  }

  if (error || !assignment) {
    return (
      <div className="assignment-completion">
        <div className="error-message">
          <h2>Error</h2>
          <p>{error || 'Assignment not found'}</p>
          <button onClick={() => navigate('/candidates/assignments')} className="btn btn-primary">
            Back to Assignments
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="assignment-completion">
      <div className="assignment-header">
        <button onClick={() => navigate('/candidates/assignments')} className="back-btn">
           Back to Assignments
        </button>
        
        <div className="assignment-info">
          <h1>{assignment.assessment?.title || 'Assessment'}</h1>
          <p>Job: {assignment.job?.title || 'Unknown Job'}</p>
          
          <div className="assignment-meta">
            <div className="meta-item">
              <strong>Valid Until:</strong> {new Date(assignment.validUntil).toLocaleString()}
            </div>
            <div className="meta-item">
              <strong>Status:</strong> 
              <span className={`status-badge ${assignment.status}`}>
                {assignment.status}
              </span>
            </div>
            {timeLeft !== null && (
              <div className={`time-left ${isExpired ? 'expired' : ''}`}>
                {isExpired ? ' Assignment Expired' : ` ${timeLeft} minutes left`}
              </div>
            )}
          </div>
        </div>
      </div>

      {isSubmitted ? (
        <div className="submitted-message">
          <h2> Assignment Completed</h2>
          <p>You have successfully submitted this assignment on {new Date(assignment.submittedAt).toLocaleString()}.</p>
          <button onClick={() => navigate('/candidates/assignments')} className="btn btn-primary">
            Back to Assignments
          </button>
        </div>
      ) : (
        <form onSubmit={handleSubmit} className="assignment-form">
          {assignment.assessment?.sections?.map((section, sectionIndex) => (
            <div key={section.id} className="section">
              <h3 className="section-title">
                {sectionIndex + 1}. {section.title}
              </h3>
              {section.description && (
                <p className="section-description">{section.description}</p>
              )}
              
              <div className="questions-container">
                {section.questions.map(renderQuestion)}
              </div>
            </div>
          ))}

          {error && (
            <div className="error-message">
              {error}
            </div>
          )}

          <div className="form-actions">
            <button
              type="button"
              onClick={() => navigate('/candidates/assignments')}
              className="btn btn-secondary"
              disabled={submitting}
            >
              Cancel
            </button>
            <button
              type="submit"
              className="btn btn-primary"
              disabled={isExpired || submitting}
            >
              {submitting ? 'Submitting...' : (isExpired ? 'Assignment Expired' : 'Submit Assignment')}
            </button>
          </div>
        </form>
      )}
    </div>
  );
};

export default AssignmentCompletion;


