import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { getCandidateAssignments } from '../services/applicationsApi';
import './CandidateAssignments.css';

const CandidateAssignments = () => {
  const navigate = useNavigate();
  const [assignments, setAssignments] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Mock candidate ID - in real app this would come from auth context
  const candidateId = 'candidate-1';

  const fetchAssignments = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await getCandidateAssignments(candidateId);
      setAssignments(response);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  }, [candidateId]);

  useEffect(() => {
    fetchAssignments();
  }, [fetchAssignments]);

  const handleStartAssignment = (assignment) => {
    navigate(`/assignments/${assignment.id}`);
  };


  const isAssignmentExpired = (validUntil) => {
    return new Date(validUntil) < new Date();
  };

  const getAssignmentStatus = (assignment) => {
    if (assignment.status === 'submitted') {
      return { text: 'Completed', class: 'completed' };
    }
    if (isAssignmentExpired(assignment.validUntil)) {
      return { text: 'Expired', class: 'expired' };
    }
    return { text: 'Pending', class: 'pending' };
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  if (loading) {
    return (
      <div className="candidate-assignments">
        <div className="loading">Loading assignments...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="candidate-assignments">
        <div className="error-message">
          <h2>Error</h2>
          <p>{error}</p>
        </div>
      </div>
    );
  }

  return (
    <div className="candidate-assignments">
      <div className="assignments-header">
        <h2>My Assignments</h2>
        <p>Complete your assessments to progress in the hiring process</p>
      </div>

      {assignments.length === 0 ? (
        <div className="no-assignments">
          <h3>No Assignments Yet</h3>
          <p>Apply for jobs to receive assessment assignments.</p>
        </div>
      ) : (
        <div className="assignments-list">
          {assignments.map((assignment) => {
            const status = getAssignmentStatus(assignment);
            const isExpired = isAssignmentExpired(assignment.validUntil);
            
            return (
              <div key={assignment.id} className={`assignment-card ${status.class}`}>
                <div className="assignment-header">
                  <h3>{assignment.job?.title || 'Unknown Job'}</h3>
                  <span className={`status-badge ${status.class}`}>
                    {status.text}
                  </span>
                </div>

                <div className="assignment-content">
                  <div className="assignment-info">
                    <div className="info-item">
                      <strong>Assessment:</strong> {assignment.assessment?.title || 'Technical Assessment'}
                    </div>
                    <div className="info-item">
                      <strong>Valid Until:</strong> {formatDate(assignment.validUntil)}
                    </div>
                    <div className="info-item">
                      <strong>Created:</strong> {formatDate(assignment.createdAt)}
                    </div>
                    {assignment.submittedAt && (
                      <div className="info-item">
                        <strong>Submitted:</strong> {formatDate(assignment.submittedAt)}
                      </div>
                    )}
                  </div>

                  <div className="assignment-actions">
                    {assignment.status === 'submitted' ? (
                      <div className="completed-message">
                         Assessment completed successfully!
                      </div>
                    ) : isExpired ? (
                      <div className="expired-message">
                         This assignment has expired
                      </div>
                    ) : (
                      <button
                        onClick={() => handleStartAssignment(assignment)}
                        className="btn btn-primary"
                      >
                        {assignment.status === 'pending' ? 'Start Assessment' : 'Continue Assessment'}
                      </button>
                    )}
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      )}

    </div>
  );
};

export default CandidateAssignments;
